.projects {
    background-color: #181818;
    color: $white;
    &__wrap {
        position: relative;
        height: 100%;
        padding-top: 5px;
        padding-bottom: 30px; }
    &__slides {
        padding-left: 2px;
        padding-right: 2px; }
    &__title {
        max-width: 250px;
        margin: auto; }
    &__subtitle {
        margin: 24px auto;
        max-width: 250px; }
    &__big {
        margin-bottom: 3px;
        img {
            display: block;
            max-width: 100%; } }
    &__mini {
        display: flex;
        justify-content: space-between;
        a {
            text-decoration: none;
            margin-right: 3px;
            &:last-child {
                margin-right: 0; } }
        img {
            display: block;
            max-width: 100%; } }
    &__it-was, &__task, &__result {
        margin: 24px 0;
        p {
            margin-bottom: 24px;
            font-size: px-to-rem(17px);
            line-height: px-to-rem(24px);
            &:first-child {
                margin-bottom: 14px;
                font-size: px-to-rem(20px);
                line-height: px-to-rem(23px); } } }
    &__result {
        p {
            margin-bottom: 0px; } }
    &__task {
        ul {
            list-style: none;
            padding: 0; }
        li {
            position: relative;
            padding-left: 16px;
            margin-bottom: 24px;
            font-size: px-to-rem(17px);
            line-height: px-to-rem(24px);
            &::after {
                position: absolute;
                top: 9px;
                left: 2px;
                content: '';
                width: 4px;
                height: 5px;
                background-color: $primary;
                transform: skew(-45deg); } } }
    &__object {
        margin: 32px 0;
        border: 1px solid #454545;
        border-radius: 5px; }
    &__item {
        padding: 15px;
        border-bottom: 1px solid #454545;
        &:last-child {
            border-bottom: none; }
        p {
            font-size: px-to-rem(14px);
            line-height: px-to-rem(16px);
            color: #787878; }
        span {
            color: $white; } }
    &__counts {
        position: absolute;
        left: 15px;
        bottom: 15px;
        padding: 10px;
        background-color: rgba(0, 0, 0, .7);
        color: #787878;
        font-size: px-to-rem(14px);
        line-height: px-to-rem(22px);
        border-radius: 5px;
        z-index: 1; }
    &__first {
        font-size: px-to-rem(20px);
        color: $white; }
    & .fp-scroller {
        padding-top: 100px;
        padding-bottom: 50px; } }
div.fp-controlArrow.fp-prev, div.fp-controlArrow.fp-next {
    top: auto;
    bottom: 25px;
    border: none;
    width: 24px;
    height: 24px; }
div.fp-controlArrow.fp-prev {
    left: calc(100% - 80px);
    background: url("../img/arrow-prev.svg"); }
div.fp-controlArrow.fp-next {
    left: calc(100% - 45px);
    background: url("../img/arrow-next.svg"); }
@media (min-width: 370px) {
    .projects {
        &__gallery {
            display: flex;
            justify-content: center; }
        &__big {
            margin-bottom: 0; }
        &__mini {
            flex-direction: column;
            margin-left: 7px;
            a {
                margin-right: 0;
                margin-bottom: 3px;
                &:last-child {
                    margin-bottom: 0; } } } } }
@media (min-width: 480px) {
    .projects {
        &__title {
            max-width: 420px; }
        &__subtitle {
            max-width: none; } } }
@media (min-width: 768px) {
    .projects {
        &__title {
            max-width: none; } } }
@media (min-width: 1024px) {
    .projects {
        &__slides & .fp-tableCell, .fp-scroller {
            display: grid;
            grid-template-columns: repeat(6, 1fr);
            grid-column-gap: 30px;
            align-content: center; }
        &__title, &__subtitle {
            text-align: left;
            grid-column: 1 / -1;
            margin: 10px 0; }
        & .fp-scrollable {
            grid-column: 1 / -1; }
        &__gallery {
            grid-column: 1 / 4;
            margin-top: 15px; }
        &__information {
            grid-column: 4 / -1; }
        &__result {
            grid-row: 3 / 4; }
        &__object {
            grid-column: 1 / -1;
            display: flex;
            flex-wrap: wrap;
            border-right: none;
            border-top: none;
            border-bottom: none;
            border-left: 1px solid #454545; }
        &__item {
            flex-basis: 33.3%;
            border: 1px solid #454545;
            margin-left: -1px;
            margin-top: -1px;
            &:last-child {
                border-bottom: 1px solid #454545;
                border-bottom-right-radius: 5px; }
            &:nth-child(1) {
                border-top-left-radius: 5px; }
            &:nth-child(3) {
                border-top-right-radius: 5px; }
            &:nth-child(4) {
                border-bottom-left-radius: 5px; } }
        &__it {}
        &__big {
            img {
                height: 100%;
                object-fit: cover; } }
        &__it-was, &__task, &__result {
            margin: 15px 0;
            p {
                font-size: px-to-rem(12px);
                line-height: px-to-rem(22px);
                &:first-child {
                    font-size: px-to-rem(18px);
                    line-height: px-to-rem(21px); } } }
        &__big, &__mini {
            max-height: 369px; }
        &__mini img {
            max-width: none; }
        &__task {
            li {
                margin-bottom: 0;
                font-size: px-to-rem(12px);
                line-height: px-to-rem(22px); } } } }
@media (min-width: 1140px) {
    .projects {
        &__it-was, &__task, &__result {
            p {
                font-size: px-to-rem(14px); }
            &__task {
                li {
                    font-size: px-to-rem(14px); } } } } }
